import React from 'react'
import { ReactElement } from 'react'

import { SubjectSummary } from '../../../components/subject/SubjectSummary'
import { SubjectContent } from '../../../components/subject/SubjectContent'
import { SubjectTitle } from '../../../components/subject/SubjectTitle'
import { SubjectKeyword } from '../../../components/subject/SubjectKeyword'
import { SubjectIdentifier } from '../../../components/subject/SubjectIdentifier'
import { Subject } from '../../../components/subject/Subject'

export function Incantation () : ReactElement {
  return (
    <Subject>
      <SubjectIdentifier>magic-spells-incantation</SubjectIdentifier>
      <SubjectTitle>Incantation</SubjectTitle>
      <SubjectSummary>
      </SubjectSummary>
      <SubjectContent>
        <p>
          <strong>L'incantation d'un sortilège</strong> peut être subdivisé en
          autant d'action que nécéssaire appellées <strong>action
          d'incantation</strong>. Lors d'une telle action, le magicien doit
          réussir un test de maîtrise de l'incantation en opposition avec un
          degré de difficulté calculé à l'avance pour pouvoir avancer ou
          finaliser son sortilège. Si le test d'incantation est un succès le
          magicien avance un peut plus dans l'évocation de son sort, si le test
          échoue, on parle alors d'échec de l'évocation, ou de <strong>
          catastrophe ésothérique</strong>.
        </p>

        <p>
          Le <strong>degré de difficulté</strong> d'une action d'incantation
          dépend du nombre de symboles à incanter en une même action. Un
          magicien est libre de choisir ce nombre de symbole au commencement de
          son action. Pour chaque symbole à incanter, la difficulté de l'action
          augmente de 4 points. L'usage de certains atouts peut réduire ce degré
          de difficulté de base.
        </p>

        <p>
          Certains <strong>malus contextuels</strong> peuvent s'additionner à la
          difficulté de base d'une incantation :
        </p>

        <ul>
          <li>
            Pour chaque blessure légère non-traité, le magicien voit augmenter
            la difficulté de son incantation de 2 point.
          </li>
          <li>
            Pour chaque blessure grave non-traité, le magicien voit augmenter
            la difficulté de son incantation de 4 points.
          </li>
          <li>
            Pour chaque adversaire au contact, le magicien voit augmenter
            la difficulté de son incantation de 4 points.
          </li>
          <li>
            Si le magicien se déplace tout en incantant, la difficulté de son
            incantation augmente de 2 points.
          </li>
        </ul>

        <p>
          Si un magicien échoue un test d'évocation, on parle alors de
          <strong>catastrophe ésothérique</strong> : dans les faits, une erreur
          dans les paroles ou la vision d'un mauvais signe ont des conséquences
          inatendues. Dans une telle situation, le magicien doit lancer 1D20 et
          éxécuter un des effets de la table suivante :
        </p>

        <table className="table-1d table-vertical">
          <thead>
            <tr>
              <th style={{width: '100px'}}> Score </th>
              <th style={{width: '300px'}}> Effet </th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td> 1 - 4 </td>
              <td>
                <strong>Dissipation</strong> <br/>
                Dans le cas d'une dissipation les effets liés à l'incantation
                cessent immédiatement et la réalité reprend sa forme initiale.
                Dans ce cas, le sortilège échoue purement et simplement sans
                effets secondaires.
              </td>
            </tr>
            <tr>
              <td> 4 - 9 </td>
              <td>
                <strong> </strong>
                Épisodes nauséeux et vertiges passagers. <br />
                Perte de deux points de dextérité pendant 1D4 jours. <br />
                Les effets peuvent être divisé par deux avec la consomation
                d'une drogue tonique de qualité 5+. <br />
                Cet effet est cumulable. <br />
                Cet effet disparrais si le niveau d'empoisonement
                est inférieur à la moitiée de la constitution du personnage.
              </td>
            </tr>
            <tr>
              <td> 10 - 14 </td>
              <td>
                Fièvre et douleurs articulaires. <br />
                Pendant 1D6 jours : <br />
                <ul>
                  <li>Perte de deux points de constitution.</li>
                  <li>Perte de deux points de contrôle.</li>
                </ul>
                Cet effet est cumulable <br />
                Cet effet peut être divisés par deux avec l'administration d'un
                anti-douleur et d'une drogue tonique de qualité 10+. <br />
                Cet effet disparrais si le niveau d'empoisonement
                est inférieur à la moitiée de la constitution du
                personnage + 3.
              </td>
            </tr>
            <tr>
              <td> 15 - 16 </td>
              <td>
                Épisode d'hypotermie. <br />
                Perte de quatre points de constitution pendant 1D6 / 3 jours. <br />
                Les effets peuvent être divisés par deux avec l'administration
                d'une drogue tonique de qualité 20+. <br />
                Cet effet est cumulable.
              </td>
            </tr>
            <tr>
              <td> 17 - 20 </td>
              <td>
                Hémoragie. <br />
                Le personnage est sujet à une hémoragie temporaire et perd 1D4
                points de vie. <br />
                Pendant 1D6/2 jours : <br />
                <ul>
                  <li>Perte de deux points de constitution.</li>
                  <li>Perte de deux points de force.</li>
                  <li>Perte de deux points de contrôle.</li>
                </ul>
              </td>
            </tr>
            <tr>
              <td> 21 - 25 </td>
              <td>
                Mutation. <br />
                Test opposé de constitution et de volonté contre le niveau
                d'empoisonement, en cas d'échec le personnage gagne un trait de
                d'adaptation à l'éther.
              </td>
            </tr>
            <tr>
              <td> 26 - 36 </td>
              <td>
                Test opposé de constitution et de volonté contre le niveau
                d'empoisonement. Si le test échoue le personnage meurt. <br />
                En cas de réussite, le même test est rejoué cette fois pour
                déterminer si le personnage est l'objet d'une mutation, en cas
                d'échec le personnage gagne un trait d'adaptation à l'éther.
                <br />
                Un personnage avec quatre mutation ou plus réalisant un score de
                26 ou plus devient une abbération d'éther et n'est plus jouable.
              </td>
            </tr>
          </tbody>
        </table>
      </SubjectContent>
    </Subject>
  )
}
